<template>
  <div class="contact">
    <NavBar :toDisplay="{ 'selectionList': true, 'studentLink': true, 'teacherLink': true, 'contactLink': true, 'tosLink': true, 'aboutLink': true }" />
    <br>
    <div class="top-marg">
      <div class="container-form">
        <h1>Contact Us</h1>
        <br>
        <div class="top-marg">
          <div class="flex space-around flex-wrap">
            <input 
              v-model="formData.firstName" 
              class="field" 
              type="text" 
              placeholder="First name"
            >
            <input 
              v-model="formData.lastName" 
              class="field" 
              type="text" 
              placeholder="Last name"
            >
            <input 
              v-model="formData.mail" 
              class="field" 
              type="email" 
              placeholder="E-Mail"
            >
          </div>
          <div class="top-marg">
            <input 
              v-model="formData.subject" 
              class="field-subjet" 
              type="text" 
              placeholder="Subject"
            >
            <br><br>
            <textarea 
              v-model="formData.message" 
              class="field-message" 
              type="text" 
              placeholder="Message"
            />
          </div>
          <div class="top-marg">
            <button 
              class="btn-form" 
              @click="sendMail"
            >Send</button>
          </div>

        </div>
        <div class=" social top-marg">
          <h3>Social - Media</h3>
          <button class="btn-social">F</button>
          <button class="btn-social">Y</button>
          <button class="btn-social">M</button>
          <button class="btn-social">W</button>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
import NavBar from '@/components/NavBar.vue';
import generalService from '../services/general-service';
import logger from '@/services/logger.js';

export default {
  name: 'Login',
  components: {
    NavBar
  },
  data() {
    return {
      formData: {
        firstName: null,
        lastName: null,
        mail: null,
        subject: null,
        message: null
      }
    };
  },
  methods: {
    sanitizeFormFields: function() {
      for (let key in this.formData) {
        if (!this.formData[key]) {
          return false;
        }
        if (key === 'mail') {
          if (!this.validEmail(this.formData[key])) {
            logger.error('Mail is not valid');
            return false;
          }
        }
      }
      return this.formData;
    },
    validEmail: function(email) {
      var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      return re.test(String(email).toLowerCase());
    },
    sendMail: async function() {
      let checkedFormData = this.sanitizeFormFields();
      if (checkedFormData) {
        // Form data is valid, initialize a request to the server
        let formattedMailData = {
          email: checkedFormData.mail,
          name: checkedFormData.firstName + ' ' + checkedFormData.lastName,
          subject: checkedFormData.subject,
          content: checkedFormData.message
        };

        let response = await generalService.sendContactUsMail(
          formattedMailData
        );
        if (response.success) {
          logger.info('Mail sent successfully.');
        } else {
          logger.warn('Mail request failed.');
        }
      } else {
        // Form data didn't pass the test function
      }
    }
  }
};
</script>

 <style lang="scss" scoped>
input {
  padding: 5px 10px 5px 10px;
}

textarea {
  padding: 5px 10px 5px 10px;
}

.field {
  min-width: 200px;
  margin-bottom: 1rem;
}

.container-form {
  padding: 1rem;
  background-color: #e2e2e298;
  margin: 0 auto;
  width: 100%;
  max-width: 820px;
  box-shadow: 3px 3px 15px -3px rgba(0, 0, 0, 0.7);
  transition: 0.3s;

  .btn-form {
    margin-top: 2rem;
    cursor: pointer;
    border: none;
    border-radius: 15px;
    background-color: rgb(194, 194, 194);
    padding: 0.5rem 1rem 0.5rem 1rem;
  }
}

.social {
  margin-top: 2rem;
  // margin: 0 auto;

  .btn-social {
    cursor: pointer;
    padding: 0.5rem;
    margin: 0.5rem;
    background-color: aqua;
    border-radius: 50%;
    border: none;
  }
}

.field-message {
  height: 100px;
  width: 350px;
}
@media (max-width: 720px) {
  .container-form {
    max-width: 100%;
    padding: 1rem 2rem 1rem 2rem;
  }

  .field {
    min-width: 200px;
    width: 100%;
  }

  .field-message {
    min-width: 200px;
    width: 100%;
  }

  h1 {
    color: aqua;
  }
}
</style>
